body {
	background: #F6F6F6;
}

.wrapper {
	max-width: 1290px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
}

.course {
	padding: 6px 0;
	background: #2B495A;
	white-space: nowrap;
	&-items {
		margin: 0 -10px;
	}
	&-item {
		padding: 0 10px;
		font-size: 14px;
		line-height: 136%;
		color: @light;

		&:not(:last-child) {
			border-right: 1px solid rgba(255, 255, 255, .24);
		}
	}

	&-name, &-price {
		margin-right: 12px;
	}

	&-diff {
		svg {
			margin-left: 6px;
		}
		&.plus {
			color: @green;
			svg {
				fill: @green;
			}
		}
		&.minus {
			color: #F95929;
			svg {
				transform: rotate(180deg);
				fill: #F95929;
			}
		}
	}
}

.header {
	position: sticky;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 4;
}

.menu {
	background: @light;
	&-items {
		padding: 15px 0;
	}
}

.nav {
	margin-left: 32px;
	&-link {
		display: inline-block;
		font-size: 18px;
		line-height: 136%;
		&:not(:last-child) {
			margin-right: 24px;
		}
	}
}

.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	padding: 12px 18px;
	border-radius: 50px;
	.medium();
	color: @light;
	font-size: 16px;
	line-height: 100%;
	svg {
		margin-right: 10px;
	}
	&-green {
		background: @green;
		border: 1.5px solid @green;
		&:hover {
			background: #208670;
			border: 1.5px solid #208670;
		}
	}
	&-gold {
		background: @golden;
		border: 1.5px solid @golden;
		&:hover {
			background: @golden;
			border: 1.5px solid @golden;
		}
	}
	&-outline-green {
		background: transparent;
		border: 1.5px solid @green;
		color: @green;
		&:hover {
			background: @green;
			color: @light;
			svg {
				fill: @light;
			}
		}
		svg {
			fill: @green;
		}
	}
	&-outline-gold {
		background: transparent;
		border: 1.5px solid @golden;
		color: @golden;
		&:hover {
			background: @golden;
			color: @light;
			svg {
				fill: @light;
			}
		}
		svg {
			fill: @golden;
		}
	}
	&-all {
		background: @light;
		border: 1.5px solid rgba(101, 97, 91, 0.32);
		border-radius: 4px;
		color: @dark;

		&:hover {
			color: @green;
			border: 1.5px solid @green;
		}
	}
}

.main {
	background-size: cover;
	background-position: top center;
	background-repeat: no-repeat;
	position: relative;
	padding-top: 73px;
	padding-bottom: 175px;
	.wrapper {
		align-self: center;
	}
	.bread {
		position: absolute;
		top: 0;
		left: 0;
	}

	&-catalog {
		min-height: 500px;
	}

	&-content {
		max-width: 660px;
	}

	.text {
		margin-top: 12px;
		max-width: 628px;
	}

	&-buttons {
		margin-top: 32px;
	}

	.btn {
		margin-right: 18px;
	}

	.btn-outline-green {
		svg {
			width: 18px;
			height: 18px;
		}
	}
	
}

.title {
	.bold();
	font-size: 52px;
	line-height: 100%;
}
.title2 {
	font-weight: bold;
	font-size: 52px;
	line-height: 124%;
}

.text {
	font-size: 18px;
	line-height: 132%;
	color: rgba(0, 0, 0, .6);
}

.login {
	margin-left: 12px;
	padding: 8px 12px;
	border-radius: 4px;
	svg {
		fill: @light;
	}
}

.events {
	margin-left: auto;
}

.event {
	padding: 0 12px;
	cursor: pointer;
	position: relative;

	&.has-prod {
		padding-right: 32px;
		svg {
			fill: @green;
		}

		.event-length {
			display: flex;
		}

		&:hover {
			.event-toggle {
				display: block;
			}
		}
	}

	svg {
		fill: rgba(0, 0, 0, .6);
	}

	&:not(:last-child) {
		border-right: 1px solid rgba(43, 73, 90, 0.12);
	}

	&:hover {
		.event-hidden {
			display: block;
		}
		svg {
			fill: @green;
		}
	}

	&-length {
		display: none;
		align-items: center;
		justify-content: center;
		position: absolute;
		width: 18px;
		height: 18px;
		border-radius: 50%;
		background: @green;
		right: 12px;
		font-size: 12px;
		line-height: 100%;
		text-align: center;
		color: @light;
	}

	&-toggle {
		display: none;
		position: absolute;
		min-width: 345px;
		width: max-content;
		left: 50%;
		top: calc(100% + 24px);
		transform: translateX(-50%);
		background: @light;
		border-radius: 8px;
		&::after {
			content: '';
			position: absolute;
			top: -30px;
			left: 0;
			width: 100%;
			border-top: 30px solid transparent;
		}
	}

	&-head {
		padding: 18px;
		border-bottom: 1px solid #EEEEEE;
		.medium();
		font-size: 18px;
		line-height: 108%;
		color: @text;
	}

	&-icon {
		justify-content: center;
		width: 30px;
		height: 30px;
		border-radius: 4px;

		color: @green;
		font-size: 16px;
		line-height: 136%;

		&.active {
			background: rgba(230, 233, 235, 0.5);
		}
	}

	&-hidden {
		display: none;
		width: 30px;
		position: absolute;
		top: 100%;
		left: 12px;
		background: @light;
		border-radius: 4px;
		box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.14);
		z-index: 2;
	}

	&-currency {
		justify-content: center;
		width: 100%;
		height: 30px;
		&:not(:last-child) {
			border-bottom: 1px solid rgba(43, 73, 90, 0.12);
		}

		&:hover {
			color: @green;
		}
	}

	&-search {
		display: none;
		background: #F9F9F9;
		width: 100%;
		position: relative;
		&.active {
			display: block;
		}
		input {
			background: transparent;
			width: 100%;
			height: 84px;
			.reg();
			font-size: 18px;
			line-height: 110%;
			color: @dark;
		}

		.wrapper {
			position: relative;
		}

		button {
			position: absolute;
			right: 15px;
			top: 50%;
			transform: translateY(-50%);
			svg {
				fill: transparent;
				stroke: @green;
				stroke-width: 1.5;
			}
		}
	}

	&-close {
		position: absolute;
		right: 32px;
		top: 50%;
		transform: translateY(-50%);
		cursor: pointer;
	}

	&-item {
		padding: 12px 32px 12px 18px;
		border-bottom: 1px solid #EEEEEE;
		position: relative;
		&:hover {
			.event-remove {
				display: block;
			}
		}
	}

	&-bottom {
		justify-content: flex-end;
		padding: 18px 32px;
		font-size: 18px;
		line-height: 108%;
		color: @text;
		.price {
			.bold();
			margin-left: 10px;
		}
	}

	&-image {
		width: 32px;
		height: 32px;
		flex-shrink: 0;
		margin-right: 12px;
	}

	&-title {
		width: 200px;
		height: 38px;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;  
		overflow: hidden;
		.medium();
		font-size: 14px;
		line-height: 136%;
		color: @text;
		margin-right: 12px;
		flex-shrink: 0;
	}

	&-elem {
		flex-shrink: 0;
		& > span {
			display: block;
			margin-bottom: 4px;
			font-size: 14px;
			line-height: 108%;
			color: rgba(32, 34, 37, 0.5);
		}
		.price {
			.medium();
			font-size: 16px;
			line-height: 120%;
			color: @text;
		}
	}

	&-remove {
		display: none;
		position: absolute;
		width: 24px;
		height: 24px;
		top: 50%;
		right: -12px;
		transform: translateY(-50%);
		svg {
			fill: transparent !important;
		}
	}
}

.lang {
	padding-left: 12px;
}

.back {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, .4);
	z-index: -1;
	&.active {
		display: block;
	}
}

.subtitle {
	text-align: center;
	.bold();
	font-size: 28px;
	line-height: 124%;
	color: @text;
}

.section {
	padding: 46px 0;
	&-wrap {
		margin: 32px -12px -24px -12px;
		flex-wrap: wrap;
	}
}

.card {
	background: @light;
	border: 1px solid rgba(101, 97, 91, 0.32);
	border-radius: 8px;
	margin: 0 12px 24px 12px;
	width: calc(100% / 4 - 24px);

	&:hover {
		filter: drop-shadow(8px 8px 20px rgba(0, 0, 0, 0.14));
		.card-fav {
			display: block;
		}
	}

	&-popular {
		width: calc(100% / 5 - 24px);
		padding: 0 15px 14px 15px;
		.card-image {
			height: auto;
		}
	}

	&-image {
		justify-content: center;
		padding: 22px 0;
		height: 204px;
		position: relative;
	}

	&-fav {
		display: none;
		position: absolute;
		top: 18px;
		right: 18px;
		cursor: pointer;
		&:hover {
			svg {
				fill: @green;
			}
		}
		&.active {
			svg {
				fill: @green;
			}
		}
		svg {
			fill: rgba(101, 97, 91, 0.6);
		}
	}

	&-name {
		.medium();
		font-size: 24px;
		line-height: 120%;
		text-align: center;
		color: #2B495A;
	}

	&-body {
		padding: 12px 22px 30px 22px;
	}

	&-title {
		height: 72px;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;  
		overflow: hidden;
		.medium();
		font-size: 18px;
		line-height: 136%;
		color: @text;
	}

	&-events {
		margin-top: 22px;
		justify-content: space-between;
		position: relative;
		&::after {
			content: '';
			position: absolute;
			left: 50%;
			top: 0;
			height: 100%;
			width: 1px;
			background: rgba(0, 0, 0, 0.08);
		}
	}

	&-event {
		width: 50%;
		text-align: center;
		&:first-child {
			padding-right: 22px;
		}
		&:last-child {
			padding-left: 22px;
		}
		&-title {
			font-size: 14px;
			line-height: 136%;
			color: @text;
		}
		&-price {
			margin-top: 4px;
			.bold();
			font-size: 18px;
			line-height: 136%;
			color: @text;
		}

		.btn {
			width: 100%;
			margin-top: 12px;
			padding: 8px 12px;
			border-radius: 4px;
		}
	}

	&.align-center {
		width: calc(100% - 24px);
		padding: 32px;
		.card-image {
			width: 64px;
			height: 64px;
			flex-shrink: 0;
			padding: 0;
			margin-right: 46px;
		}
		.card-fav {
			top: -16px;
			right: -16px;
		}
		.card-body {
			width: 100%;
			padding: 0;
			display: flex;
			align-items: center;
		}
		.card-title {
			height: auto;
			max-width: 520px;
			margin-right: 44px;
		}
		.card-events {
			margin-top: 0;
			margin-left: auto;
		}
		.card-event {
			display: flex;
			align-items: center;
		}
		.card-event:first-child {
			padding-right: 12px;
		}
		.card-event-price {
			margin: 0 12px 0 6px;
			flex-shrink: 0;
		}
		.card-event .btn {
			margin-top: 0;
		}
	}
}

.all {
	margin-top: 24px;
	text-align: center;
}

.banner {
	padding: 46px 0;
	&-block {
		min-height: 400px;
		position: relative;
		background: #E8E9EA;
		border: 1px solid rgba(101, 97, 91, 0.32);
		border-radius: 8px;
		padding: 76px;
	}
	&-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
	}

	&-content {
		max-width: 435px;
		position: relative;
		z-index: 2;
	}

	&-title {
		max-width: 400px;
		.bold();
		font-size: 32px;
		line-height: 110%;
	}

	.text {
		margin-top: 12px;
	}

	&-light {
		.banner-title {
			color: @light;
		}
		.text {
			color: @light;
		}

		.more {
			margin-top: 32px;
			padding: 12px 18px;
			background: @light;
			color: @dark;
			font-size: 18px;
			line-height: 132%;
			border: none;
			box-shadow: 4px 4px 24px rgba(0, 0, 0, 0.12);
			border-radius: 4px;
		}
	}
}

.more {
	display: inline-block;
	margin-top: 48px;
	.medium();
	font-size: 20px;
	line-height: 112%;
	border-bottom: 2px solid rgba(0, 0, 0, .24);
}

.footer {
	margin-top: 18px;
	background: @text;
	&__top {
		padding: 52px 0 32px 0;
		border-bottom: 1px solid rgba(238, 238, 238, 0.1);
		display: flex;
	}
	&__nav {
		.footer__contacts-name {
			text-align: left;
		}
	}
	&-col {
		margin-right: 64px;
		column-count: 2;
		max-width: 500px;
		list-style: none;
		& li {
			list-style: none;
			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}
	}
	&-link {
		display: block;
		.reg();
		font-size: 16px;
		line-height: 112%;
		color: #96A2AF;
		&:not(:last-child) {
			margin-bottom: 10px;
		}
		&:hover {
			color: #DCE6F1;
		}
	}

	&__social {
		.footer__contacts-name {
			text-align: left;
		}
		&-links {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
		}
	}

	.social-link {
		display: inline-block;
		margin-bottom: 10px;
		margin-right: 12px;
		svg {
			fill: #96A2AF;
		}
		&:hover {
			svg {
				fill: #DCE6F1;
			}
		}
	}

	&__contacts {
		margin-left: auto;
		max-width: 400px;
		&-name {
			.medium();
			font-size: 18px;
			line-height: 108%;
			color: @light;
			text-align: right;
			margin-bottom: 18px;
		}
		&-items {
			text-align: right;
			.phone {
				display: inline-block;
				.reg();
				font-size: 28px;
				line-height: 124%;
				color: @light;
			}
			address {
				margin-top: 4px;
				.reg();
				font-size: 16px;
				line-height: 112%;
				color: #96A2AF;
			}
			.mail {
				display: inline-block;
				margin-top: 8px;
				.reg();
				font-size: 16px;
				line-height: 112%;
				color: #96A2AF;
				&:hover {
					color: #DCE6F1;
				}
			}
		}
	}

	&__bottom {
		padding: 25px 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		.copyright {
			span {
				display: block;
				.reg();
				font-size: 14px;
				line-height: 136%;
				color: #96A2AF;
				opacity: 0.6;
			}
		}
		.up {
			cursor: pointer;
			transform: translateX(75px);
			svg {
				fill: #96A2AF;
			}
		}
		.alert {
			text-align: right;
			span {
				display: block;
				.reg();
				font-size: 14px;
				line-height: 136%;
				color: #96A2AF;
				opacity: 0.6;
			}
		}
	}

	.pay {
		margin-top: 26px;
		justify-content: flex-end;
		padding: 0;

		svg {
			margin-left: 10px;
		}
	}
}

.seo {
	padding: 46px 0;
	.subtitle {
		text-align: left;
	}
	.text {
		margin-top: 18px;
		max-height: 150px;
		overflow: hidden;
		transition: .5s;
		&.active {
			max-height: 100%;
			transition: .5s;
		}
		p {
			&:not(:last-child) {
				margin-bottom: 16px;
			}
		}
	}

	.more {
		margin-top: 24px;
		font-size: 18px;
		line-height: 100%;
		color: #36393F;
	}
}

.advantage {
	padding: 24px;
	background: @light;
	border-radius: 8px;
	width: calc(100% / 3 - 24px);
	margin: 0 12px 24px 12px;

	&-title {
		margin-top: 24px;
		.medium();
		font-size: 20px;
		line-height: 124%;
	}
	&-desc {
		margin-top: 8px;
		font-size: 16px;
	}
}

.news {
	margin: 0 12px 24px 12px;
	width: calc(100% / 4 - 24px);
