		}
	}

	&-title {
		margin-top: 15px;
		.bold();
		font-size: 18px;
		line-height: 136%;
		color: @text;
	}
	&-desc {
		margin-top: 4px;
		height: 88px;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;  
		overflow: hidden;
		font-size: 16px;
		line-height: 136%;
		color: @text;
	}
	&-date {
		margin-top: 12px;
		font-size: 14px;
		line-height: 136%;
		color: rgba(32, 34, 37, 0.5);
	}
}

.call {
	justify-content: center;
	width: 64px;
	height: 64px;
	right: 40px;
	bottom: 40px;
	border-radius: 50%;
	position: fixed;
	background: @green;
	z-index: 4;
	svg {
		width: 30px;
		height: 30px;
		fill: @light;
	}
}

.bread {
	width: 100%;
	padding: 24px 0;
	&-link {
		display: inline-flex;
		align-items: center;
		font-size: 14px;
		line-height: 136%;
		color: #999999;
		&:last-child {
			color: rgba(43, 73, 90, .6);
		}
		&:not(:last-child) {
			&::after {
				content: url('../img/general/arrow-right.svg');
				display: inline-block;
				margin: 0 12px;
			}
		}
	}
}


.about {
	margin-top: 46px;
	background: @light;
	border-radius: 8px;
	&-image {
		flex-shrink: 0;
		position: relative;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: -24px;
			width: 160px;
			height: 100%;
			background: linear-gradient(90deg, @light 26.4%, rgba(255, 255, 255, 0) 95.34%);
		}

		img {
			display: block;
		}
	}
	&-info {
		padding: 64px;
	}
	&-title {
		.bold();
		font-size: 32px;
		line-height: 100%;
		color: @text;
	}
	.text {
		font-size: 16px;
		color: rgba(54, 57, 63, 0.8);
		p {
			margin-top: 16px;
		}
	}
}

.numbers {
	justify-content: space-around;
}

.number {
	&-top {
		.medium();
		font-size: 48px;
		line-height: 100%;
		color: @green;
	}
	&-text {
		margin-top: 4px;
		font-size: 20px;
		line-height: 100%;
		color: #65615B;
	}
}

.advant {
